<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Pengaturan</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-refresher slot="fixed" @ionRefresh="doRefresh($event)">
        <ion-refresher-content />
      </ion-refresher>
      <ion-content class="ion-padding">
        <!-- Profile Picture -->
        <div class="profile-picture-container">
          <img
            :src="profilePicture || defaultAvatar"
            alt="Profile Picture"
            class="profile-picture"
          />
          <label for="file-upload" class="upload-button"
            >Ganti Foto Profil</label
          >
          <input
            id="file-upload"
            type="file"
            @change="uploadProfilePicture"
            accept="image/*"
          />
        </div>
        <!-- User Information -->
        <ion-item>
          <ion-label position="floating">Nama Lengkap</ion-label>
          <ion-input v-model="fullName"></ion-input>
        </ion-item>
        <ion-item>
          <ion-label position="floating">Email</ion-label>
          <ion-input v-model="email" type="email"></ion-input>
        </ion-item>
        <ion-item>
          <ion-label position="floating">Username</ion-label>
          <ion-input v-model="username"></ion-input>
        </ion-item>
        <ion-item>
          <ion-label position="floating">NISN</ion-label>
          <ion-input v-model="nisn"></ion-input>
        </ion-item>
        <ion-item>
          <ion-label position="floating">Nama Sekolah</ion-label>
          <ion-input v-model="schoolName"></ion-input>
        </ion-item>
        <ion-item>
          <ion-label position="floating">Kelas</ion-label>
          <ion-select
            v-model="userClass"
            placeholder="Pilih Kelas"
            :disabled="isClassLocked"
            @ionChange="checkClassChange"
          >
            <ion-select-option
              v-for="kelas in classes"
              :key="kelas.id"
              :value="kelas.id"
            >
              {{ kelas.name }}
            </ion-select-option>
          </ion-select>
        </ion-item>
        <ion-item>
          <ion-label position="floating">Role</ion-label>
          <ion-input v-model="role" readonly></ion-input>
        </ion-item>
        <ion-button style="color: white" @click="updateUserInfo" expand="block"
          >Perbarui Informasi Pengguna</ion-button
        >
        <!-- QR Code Registration -->
        <ion-item>
          <ion-label position="floating">Kode QR</ion-label>
          <ion-input
            v-model="qrCode"
            placeholder="Hasil scan QR Code"
            readonly
          ></ion-input>
        </ion-item>
        <ion-button style="color: white" @click="scanQRCode" expand="block"
          >Scan QR Code</ion-button
        >
        <ion-button style="color: white" @click="registerQRCode" expand="block"
          >Daftarkan Kode QR</ion-button
        >
      </ion-content>
      <ion-grid class="ion-padding">
        <!-- Additional content for the grid can be added here -->
      </ion-grid>
      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>
    </ion-content>
  </ion-page>
</template>

<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonContent,
  IonRefresher,
  IonRefresherContent,
  IonGrid,
  IonItem,
  IonLabel,
  IonInput,
  IonSelect,
  IonSelectOption,
  IonButton,
  IonToast,
} from "@ionic/vue";
import { ref, onMounted } from "vue";
import {
  getFirestore,
  doc,
  getDoc,
  updateDoc,
  getDocs,
  collection,
} from "firebase/firestore";
import { getAuth } from "firebase/auth";
import {
  getStorage,
  ref as storageRef,
  uploadBytes,
  getDownloadURL,
} from "firebase/storage";
import { BarcodeScanner } from "@capacitor-community/barcode-scanner";
import { firebaseApp } from "../firebase.js";

const defaultAvatar = require("@/views/avatar.png");
const db = getFirestore(firebaseApp);
const storage = getStorage(firebaseApp);
const fullName = ref("");
const email = ref("");
const username = ref("");
const userClass = ref("");
const role = ref("");
const qrCode = ref("");
const profilePicture = ref(defaultAvatar);
const classes = ref([]);
const nisn = ref("");
const schoolName = ref("");
const isClassLocked = ref(false); // To check if class is locked
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});

const fetchUserData = async () => {
  const auth = getAuth(firebaseApp);
  const user = auth.currentUser;
  if (!user) {
    showToast("Pengguna tidak terautentikasi", "danger");
    return;
  }

  try {
    const userDoc = await getDoc(doc(db, "users", user.uid));
    if (userDoc.exists()) {
      const userData = userDoc.data();
      fullName.value = userData.fullName || "";
      email.value = userData.email || "";
      username.value = userData.username || "";
      userClass.value = userData.kelas || "";
      role.value = userData.role || "";
      profilePicture.value = userData.profilePicture || defaultAvatar;
      qrCode.value = userData.qrCode || "";
      nisn.value = userData.nisn || "";
      schoolName.value = userData.schoolName || "";
      isClassLocked.value = !!userData.kelas; // Lock the class input if a class is already set
    } else {
      showToast("Data pengguna tidak ditemukan", "danger");
    }
  } catch (error) {
    console.error("Error fetching user data:", error);
    showToast("Terjadi kesalahan saat mengambil data pengguna", "danger");
  }
};

const fetchClasses = async () => {
  const querySnapshot = await getDocs(collection(db, "kelas"));
  classes.value = querySnapshot.docs.map((doc) => ({
    id: doc.id,
    ...doc.data(),
  }));
};

const scanQRCode = async () => {
  try {
    await BarcodeScanner.checkPermission({ force: true });
    await BarcodeScanner.hideBackground(); // make background of WebView transparent
    const result = await BarcodeScanner.startScan(); // start scanning and wait for a result
    if (result.hasContent) {
      qrCode.value = result.content; // Save the scanned content to qrCode
      showToast("QR Code berhasil dipindai", "success");
    }
  } catch (error) {
    console.error("Error scanning QR code:", error);
    showToast("Error scanning QR code", "danger");
  } finally {
    BarcodeScanner.showBackground();
    BarcodeScanner.stopScan();
  }
};

const registerQRCode = async () => {
  const auth = getAuth(firebaseApp);
  const user = auth.currentUser;
  if (!user) {
    showToast("Pengguna tidak terautentikasi", "danger");
    return;
  }

  if (qrCode.value.trim() === "") {
    showToast("Kode QR tidak boleh kosong", "danger");
    return;
  }

  try {
    await updateDoc(doc(db, "users", user.uid), {
      qrCode: qrCode.value,
    });
    showToast("Kode QR berhasil didaftarkan", "success");
  } catch (error) {
    console.error("Error registering QR code:", error);
    showToast("Gagal mendaftarkan kode QR", "danger");
  }
};

const updateUserInfo = async () => {
  const auth = getAuth(firebaseApp);
  const user = auth.currentUser;
  if (!user) {
    showToast("Pengguna tidak terautentikasi", "danger");
    return;
  }

  try {
    await updateDoc(doc(db, "users", user.uid), {
      fullName: fullName.value,
      email: email.value,
      username: username.value,
      kelas: userClass.value,
      profilePicture: profilePicture.value,
      nisn: nisn.value,
      schoolName: schoolName.value,
    });
    showToast("Informasi pengguna berhasil diperbarui", "success");
  } catch (error) {
    console.error("Error updating user info:", error);
    showToast("Gagal memperbarui informasi pengguna", "danger");
  }
};

const uploadProfilePicture = async (event) => {
  const file = event.target.files[0];
  if (!file) return;

  const auth = getAuth(firebaseApp);
  const user = auth.currentUser;
  if (!user) {
    showToast("Pengguna tidak terautentikasi", "danger");
    return;
  }

  const storageReference = storageRef(storage, `profile_pictures/${user.uid}`);
  try {
    await uploadBytes(storageReference, file);
    const downloadURL = await getDownloadURL(storageReference);
    profilePicture.value = downloadURL;
    showToast("Foto profil berhasil diunggah", "success");
    // Update user document with new profile picture URL
    await updateDoc(doc(db, "users", user.uid), {
      profilePicture: downloadURL,
    });
  } catch (error) {
    console.error("Error uploading profile picture:", error);
    showToast("Gagal mengunggah foto profil", "danger");
  }
};

const checkClassChange = (event) => {
  if (isClassLocked.value) {
    showToast("Tidak bisa mengubah kelas. Hubungi admin.", "danger");
    userClass.value = event.target.value;
  }
};

const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message,
    duration: 2000,
    color,
  };
};

const doRefresh = (event) => {
  fetchUserData();
  fetchClasses();
  event.target.complete();
};

onMounted(() => {
  fetchUserData();
  fetchClasses();
});
</script>

<style scoped>
.profile-picture-container {
  text-align: center;
  margin-bottom: 20px;
}

.profile-picture {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
}

.upload-button {
  display: block;
  margin: 10px auto;
  cursor: pointer;
  color: #007bff;
}

#file-upload {
  display: none;
}
</style>
